import React, {Component, useState} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import styled from '@emotion/styled';

import {Location} from '@reach/router';
import {parse} from 'query-string';

import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import {Formik} from 'formik';
import * as Yup from 'yup';

import DefaultLayout from 'components/layouts/DefaultLayout';
import Title from 'components/Title';
import createUrl from 'utils/createUrl';
import navigation from '../../data/navigation/primary.yml';

import {Applications} from 'agent';

type Props = {};

const TitleRow = styled(Row)`
  text-align: center;
  margin: auto;
`;

const ContentRow = styled(Row)`
  text-align: center;
  max-width: 60rem;
  margin: auto;
`;

const Text = styled.p`
  font-size: x-large;
`;

const today = new Date();

const schema = Yup.object({
  token: Yup.string()
    .required('Token ist erforderlich!')
    .max(500, 'Das Token darf maximal 500 Zeichen lang sein')
    .matches(
      /^[A-Za-z0-9-_=]+\.[A-Za-z0-9-_=]+\.?[A-Za-z0-9-_.+/=]*$/,
      'Das Token enthält ungültige Zeichen!',
    ),
});

function submitForm(data) {
  const response = Applications.verify(data).then(
    function onResult(res) {
      alert('Verifizierung war erfolgreich!');
      window.widget.setState({success: true});
    },
    function onError(err) {
      var error =
        'Die Verifizierung war nicht erfolgriech! Das Token ist ungültig oder abgelaufen. Bei Problemen wende dich bitte an den Support.';
      alert(error);
      window.widget.setState({error: error});
    },
  );
}
var notSend = true;

const setNotSend = () => {
  notSend = false;
  return true;
};

function ApplicationVerificationForm() {
  return (
    <Location>
      {({navigate, location}) => (
        <Formik
          validationSchema={schema}
          onSubmit={submitForm}
          initialValues={{
            token: parse(location.search).token || '',
          }}>
          {({
            handleSubmit,
            handleChange,
            handleBlur,
            values,
            touched,
            isValid,
            errors,
            submitForm,
          }) => (
            <Form noValidate onSubmit={handleSubmit}>
              <Form.Row>
                <Form.Group as={Col} md="12" controlId="validationToken">
                  <Form.Label>Token</Form.Label>
                  <Form.Control
                    type="text"
                    name="token"
                    value={values.token}
                    onChange={handleChange}
                    isInvalid={!!errors.token}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.token}
                  </Form.Control.Feedback>
                </Form.Group>
              </Form.Row>
              <Button type="submit">Verifizieren</Button>
              {notSend &&
                parse(location.search).token &&
                setNotSend() &&
                submitForm()}
            </Form>
          )}
        </Formik>
      )}
    </Location>
  );
}

class ApplicationVerificationWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {success: false, error: false};
  }

  componentDidMount() {
    window.widget = this;
  }

  render() {
    if (!this.state.success) {
      return (
        <div>
          <Title
            title="Verifizierung - Die Föderalen"
            url={createUrl('/verifizierung')}
          />
          <TitleRow>
            <Col>
              <h1>Verifizierung</h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>
                Nutze das Token aus deiner E-Mail, um die E-Mail-Adresse zu
                verifizieren. Solltest trotz erfolgreicher Übermittlung deines
                Mitgliedantrags noch keine E-Mail erhalten haben, dann wende
                dich bitte an den Support.
              </Text>
              {this.state.error && (
                <ContentRow>
                  <Col>
                    <Alert variant="danger">{this.state.error}</Alert>
                  </Col>
                </ContentRow>
              )}
              <ApplicationVerificationForm />
            </Col>
          </ContentRow>
        </div>
      );
    } else {
      return (
        <div>
          <Title
            title="Die Verifizerung war erfolgreich - Die Föderalen"
            url={createUrl('/verifizierung')}
          />
          <TitleRow>
            <Col>
              <h1>Die Verifizerung war erfolgreich!</h1>
            </Col>
          </TitleRow>
          <ContentRow>
            <Col>
              <Text>Vielen Dank! Die Verifizierung war erfolgreich!</Text>
            </Col>
          </ContentRow>
        </div>
      );
    }
  }
}

class Verifizierung extends Component<Props> {
  render() {
    return (
      <DefaultLayout navigation={navigation}>
        <ApplicationVerificationWidget />
      </DefaultLayout>
    );
  }
}

export default Verifizierung;
